import React, { useState, useEffect } from 'react';
import './App.css';

function App() {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [aiWins, setAiWins] = useState([]);
  const [goals, setGoals] = useState([]);
  const [newWin, setNewWin] = useState('');
  const [newGoal, setNewGoal] = useState({ title: '', quarter: 'Q1' });

  // Load data from localStorage on mount
  useEffect(() => {
    const savedWins = localStorage.getItem('aiWins');
    const savedGoals = localStorage.getItem('aiGoals');
    if (savedWins) setAiWins(JSON.parse(savedWins));
    if (savedGoals) setGoals(JSON.parse(savedGoals));
  }, []);

  // Save data to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('aiWins', JSON.stringify(aiWins));
  }, [aiWins]);

  useEffect(() => {
    localStorage.setItem('aiGoals', JSON.stringify(goals));
  }, [goals]);

  const addWin = () => {
    if (newWin.trim()) {
      setAiWins([...aiWins, { id: Date.now(), text: newWin, date: new Date().toLocaleDateString() }]);
      setNewWin('');
    }
  };

  const addGoal = () => {
    if (newGoal.title.trim()) {
      setGoals([...goals, { id: Date.now(), ...newGoal }]);
      setNewGoal({ title: '', quarter: 'Q1' });
    }
  };

  const deleteWin = (id) => {
    setAiWins(aiWins.filter(win => win.id !== id));
  };

  const deleteGoal = (id) => {
    setGoals(goals.filter(goal => goal.id !== id));
  };

  return (
    <div className="App">
      <header className="header">
        <div className="logo">
          <span className="sparkle"></span>
          <span className="logo-text">AI-Ready Leader Planner</span>
        </div>
      </header>

      <div className="container">
        <nav className="sidebar">
          <button 
            className={activeTab === 'dashboard' ? 'nav-item active' : 'nav-item'}
            onClick={() => setActiveTab('dashboard')}
          >
             Dashboard
          </button>
          <button 
            className={activeTab === 'wins' ? 'nav-item active' : 'nav-item'}
            onClick={() => setActiveTab('wins')}
          >
             AI Wins
          </button>
          <button 
            className={activeTab === 'goals' ? 'nav-item active' : 'nav-item'}
            onClick={() => setActiveTab('goals')}
          >
             Goals
          </button>
        </nav>

        <main className="content">
          {activeTab === 'dashboard' && (
            <div className="dashboard">
              <h1>Your AI Journey Dashboard</h1>
              
              <div className="stats-grid">
                <div className="stat-card navy">
                  <div className="stat-icon"></div>
                  <div className="stat-value">{aiWins.length}</div>
                  <div className="stat-label">Total AI Wins</div>
                </div>
                
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-value">{goals.length}</div>
                  <div className="stat-label">Active Goals</div>
                </div>
                
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-value">{aiWins.filter(w => {
                    const winDate = new Date(w.date);
                    const weekAgo = new Date();
                    weekAgo.setDate(weekAgo.getDate() - 7);
                    return winDate >= weekAgo;
                  }).length}</div>
                  <div className="stat-label">This Week</div>
                </div>
              </div>

              <div className="tip-box">
                <span className="tip-icon"></span>
                <span>Tip: Log at least one AI win per week to build momentum!</span>
              </div>
            </div>
          )}

          {activeTab === 'wins' && (
            <div className="wins-section">
              <h1>AI Wins Log</h1>
              
              <div className="input-section">
                <input
                  type="text"
                  value={newWin}
                  onChange={(e) => setNewWin(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && addWin()}
                  placeholder="Describe your AI win..."
                  className="win-input"
                />
                <button onClick={addWin} className="add-button">Add Win</button>
              </div>

              <div className="wins-list">
                {aiWins.length === 0 ? (
                  <p className="empty-state">No AI wins logged yet. Start by adding your first win above!</p>
                ) : (
                  aiWins.map(win => (
                    <div key={win.id} className="win-item">
                      <div className="win-content">
                        <div className="win-text">{win.text}</div>
                        <div className="win-date">{win.date}</div>
                      </div>
                      <button onClick={() => deleteWin(win.id)} className="delete-button"></button>
                    </div>
                  ))
                )}
              </div>
            </div>
          )}

          {activeTab === 'goals' && (
            <div className="goals-section">
              <h1>Quarterly Goals</h1>
              
              <div className="input-section">
                <input
                  type="text"
                  value={newGoal.title}
                  onChange={(e) => setNewGoal({...newGoal, title: e.target.value})}
                  onKeyPress={(e) => e.key === 'Enter' && addGoal()}
                  placeholder="What's your AI implementation goal?"
                  className="goal-input"
                />
                <select 
                  value={newGoal.quarter}
                  onChange={(e) => setNewGoal({...newGoal, quarter: e.target.value})}
                  className="quarter-select"
                >
                  <option value="Q1">Q1 2026</option>
                  <option value="Q2">Q2 2026</option>
                  <option value="Q3">Q3 2026</option>
                  <option value="Q4">Q4 2026</option>
                </select>
                <button onClick={addGoal} className="add-button">Add Goal</button>
              </div>

              <div className="goals-grid">
                {['Q1', 'Q2', 'Q3', 'Q4'].map(quarter => (
                  <div key={quarter} className="quarter-section">
                    <h3>{quarter} 2026</h3>
                    <div className="goals-list">
                      {goals.filter(g => g.quarter === quarter).length === 0 ? (
                        <p className="empty-state">No goals set for {quarter}</p>
                      ) : (
                        goals.filter(g => g.quarter === quarter).map(goal => (
                          <div key={goal.id} className="goal-item">
                            <div className="goal-text">{goal.title}</div>
                            <button onClick={() => deleteGoal(goal.id)} className="delete-button"></button>
                          </div>
                        ))
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </main>
      </div>
    </div>
  );
}

export default App;
